@import "src/common/style/variables";

.container {
  width: 100%;
  height: 36px;
  border: 1px solid $dark300;
  border-radius: 5px;
  display: flex;
  align-items: center;

  &:hover {
    border-color: $dark100;
  }

  .input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: $primaryFontColor;
    font-size: 16px;
    line-height: 24px;
  }
}